@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono);
@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
    background: linear-gradient(45deg, #57003f 0%, #f57453 100%);
    font-family: 'Ubuntu';
    overflow: hidden;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.Terminal {
    margin: 10px;
    box-shadow: 2px 4px 10px rgba(0, 0, 0, .5);
}

.TerminalBody {
    background: rgba(56, 4, 40, .9);
    height: calc(100% - 25px);
    margin-top: -1px;
    padding-top: 2px;
    font-family: 'Ubuntu mono', serif;
    overflow: scroll;
    padding-bottom: 20px;
    padding-right: 20px;
}

.TerminalBodyUser {
    color: #87d441;
}

.TerminalLocation {
    color: #6d85a9;
}

.TerminalDollar {
    color: #ddd;
}

.TerminalPromptCursor {
    width: 8px;
    background: white;
    display: block;
    -webkit-animation: 2000ms ease infinite alternate blink;
            animation: 2000ms ease infinite alternate blink;
}

@-webkit-keyframes blink {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes blink {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.TerminalBodyText {
    white-space: pre-wrap;
    color: #ddd;
}

.TerminalBodyText a:link, .TerminalBodyText a:visited, .TerminalBodyText a {
    color: #ddd;
    text-decoration: none;
}

.TerminalCommand {
    margin-left: 8px;
    color: #ddd;
    white-space: pre;
}

.TerminalPrompt {
    margin-top: 5px;
    display: flex;
}

.TerminalHeader {
    background: -webkit-gradient(linear, left top, left bottom, from(#504b45), to(#3c3b37));
    background: linear-gradient(#504b45 0%, #3c3b37 100%);
    width: 100%;
    padding: 0 8px;
    box-sizing: border-box;
    height: 25px;
    display: flex;
    align-items: center;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
}

.TerminalButton {
    width: 12px;
    height: 12px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    padding: 0;
    font-size: 7px;
    background: -webkit-gradient(linear, left top, left bottom, from(#7d7871), to(#595953));
    background: linear-gradient(#7d7871 0%, #595953 100%);
    text-shadow: 0px 1px 0px rgba(255, 255, 255, 0.2);
    box-shadow: 0px 0px 1px 0px #41403A, 0px 1px 1px 0px #474642;
    border: none;
    margin-right: 4px;

}

.TerminalButtonExit {
    background: #f25d2b;
    background: -webkit-gradient(linear, left top, left bottom, from(#f37458), to(#de4c12));
    background: linear-gradient(#f37458 0%, #de4c12 100%);
    background-clip: padding-box;
}

.TerminalButton:focus {
    outline: none;
}

.TerminalHeaderUser {
    color: #d5d0ce;
    margin-left: 4px;
    font-size: 12px;
    line-height: 14px;
}

